*, *:hover {
  -webkit-transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear;
  -khtml-transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear;
  -moz-transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear;
  -ms-transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear;
  -o-transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear;
  transition: background-color 0.25s linear, color 0.05s linear, opacity 0.25s linear, filter 0.25s linear, visibility 0.25s linear, transform 0.25s linear; }

.radio, .checkbox {
  display: inline-block;
  margin-top: 0px;
  padding-top: 0px;
  margin-bottom: 0px; }

.container {
  width: 75em;
  margin: 0 auto; }

.slide-up-enter-active, .slide-up-leave-active {
  transition: all 0.4s cubic-bezier(0, 1.2, 1, 0.5);
  opacity: .7;
  transform: translate3d(0, 4em, 0); }

.slide-up-enter, .slide-up-leave-active {
  opacity: .3;
  transform: translate3d(0, 4em, 0); }

.slide-left-enter-active, .slide-left-leave-active {
  transition: all 0.2s cubic-bezier(0, 1.2, 1, 0.5);
  opacity: .5;
  transform: translate3d(2em, 0, 0); }

.slide-left-enter, .slide-left-leave-active {
  opacity: .3;
  transform: translate3d(2em, 0, 0); }

.slide-right-enter-active, .slide-right-leave-active {
  transition: all 0.4s cubic-bezier(0, 1.2, 1, 0.5);
  opacity: .5;
  transform: translate3d(5em, 0, 0); }

.slide-right-enter, .slide-right-leave-active {
  opacity: .3;
  transform: translate3d(5em, 0, 0); }

.fade-enter-active, .fade-leave-active {
  transition: opacity .8s; }

.fade-enter, .fade-leave-active {
  opacity: 0; }

.fade-move {
  transition: transform 1s; }



